/* montserrat-200 - latin_cyrillic */
@font-face {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 200;
    src: url('../fonts/montserrat-v15-latin_cyrillic-200.eot'); /* IE9 Compat Modes */
    src: local(''), url('../fonts/montserrat-v15-latin_cyrillic-200.eot?#iefix') format('embedded-opentype'),
        /* IE6-IE8 */ url('../fonts/montserrat-v15-latin_cyrillic-200.woff2') format('woff2'),
        /* Super Modern Browsers */ url('../fonts/montserrat-v15-latin_cyrillic-200.woff') format('woff'),
        /* Modern Browsers */ url('../fonts/montserrat-v15-latin_cyrillic-200.ttf') format('truetype'),
        /* Safari, Android, iOS */ url('../fonts/montserrat-v15-latin_cyrillic-200.svg#Montserrat') format('svg'); /* Legacy iOS */
}
